import React from 'react';
import {HashRouter as Router,Redirect,Route,NavLink,Switch} from 'react-router-dom'
import Header from './Header';
import Home from './Home';
import Doc from './Doc';
import Tuto from './Tuto';
import Blog from './Blog';
import Comm from './Comm';
import Login from './Login';
import Routes from './Router.config';
function PrivateRoute({ component:Com, ...rest }) {
	return (
	  <Route
		{...rest}
		render={({ location }) =>
		   localStorage.getItem('userinfo')
		   ?<Com {...rest}/> 
		   : (
			<Redirect
			  to={{
				pathname: "/login",
				state: location 
			  }}
			/>
		  )
		}
	  />
	);
  }
  export const RouteWithSubRoutes=(route)=>{
	if(route.pri){
		return(
			<PrivateRoute {...route} routes={route.routes}></PrivateRoute>
		)
	}else{
		return (
		<Route
		  path={route.path}
		  render={props => (
			// pass the sub-routes down to keep nesting
			<route.component {...props} routes={route.routes} />
		  )}
		/>
	  );
	}
	
  }
 const App=()=>{
    return <Router>
		<Header />
        <Switch>  
			
			{
				Routes.map(({...rest})=>{
					return <RouteWithSubRoutes {...rest} />
				})
			}
        </Switch>
       
    </Router>
}

export default App;

























